
import React, { Component } from 'react';

class DirectoryForm extends Component {
    constructor(props){
        super(props);
        this.person = this.props;
    }
    render() {
        const {newName, newPhone, onChangeName, onHandleSubmit, onChangePhone} = this.props;
        return (
            <form onSubmit = {onHandleSubmit}>
            <div>
              nimi: <input value={newName} onChange={onChangeName}/>
            </div>
            <div>
              numero: <input value={newPhone} onChange={onChangePhone}/>
              </div>
            <div>
              <button type="submit" >lisää</button>
            </div>
          </form>
        );
    }         
    
}

export default DirectoryForm;


