
import React, { Component } from 'react';

class DirectoryEntry extends Component {
    constructor(props){
        super(props);
        this.person = this.props;
    }
    render() {
        const {person, onHandleDelete} = this.props;
        return (
          <div>
              <h2>Numerot</h2>
            {person.map(p => 
            <p key={p.id}>{ p.name } {p.number} <button onClick={onHandleDelete(p.id, p.name)}>Delete</button></p> 
            )}
          </div>  
        );
    }         
    
}

export default DirectoryEntry;


