import React, { Component } from 'react';
import DirectoryEntry from './DirectoryEntry';
import DirectoryForm from './DirectoryForm';

import axios from 'axios';

const baseUrl = '/api/persons'

class App extends Component {
  constructor(props){
    super(props)
    this.state = {
      person: [],
      newName: "",
      newPhone: ""
    }
  }

componentDidMount(){
  axios.get(baseUrl)
        .then(res => {
          this.setState({person: res.data});
        })
}

  //handle name change
handleNameChange = (e) => {
  //console.log(e.target.value);
  this.setState({
    newName : e.target.value
  })
}

//handle phone change
handlePhoneChange = (e) => {
  this.setState({newPhone : e.target.value})
}

//handle delete
handleDelete = (id, name)  => {

  return () => {
    window.confirm("Delete "+ name +"?");
    console.log(typeof(id), id)
    axios.delete(`api/persons/${id}`)
    .then(res=> {
      console.log(res.data);  
    });    
    const persons = this.state.person.filter(p => p.id !==id);
    this.setState({person : persons})
  }
}


  //handle submit
handleSubmit = (e) => {
  e.preventDefault();
  //check if name is present, error message, else add the name
  if(this.state.person.some(p=> p.name === this.state.newName)){
    alert("Name already exists!");
  } else {

  //new person obj
  const personObject = {
    name: this.state.newName,
    number : this.state.newPhone
  }
  
  //POST data to json-server
  axios.post(baseUrl, personObject)
        .then(res=> {
          this.setState({person: this.state.person.concat(res.data), 
            newName: '', 
            newPhone: ''});
        })

  }
}



  render() {
    const { person, newName, newPhone } = this.state;
    return (
      <div className="App">
        <h2>Puhelinluettelo</h2>
        
        <DirectoryForm newName={newName} newPhone={newPhone} 
                        onChangeName={this.handleNameChange} 
                        onChangePhone={this.handlePhoneChange} 
                        onHandleSubmit= {this.handleSubmit} />
        
        <DirectoryEntry person={person} onHandleDelete={this.handleDelete}/>
        
      </div>
    );
  }
}

export default App;
